import React from 'react'
import './hoja-vida.scss'
import Page from './Page'
import { GatsbyImage } from "gatsby-plugin-image";

const HojaVida = ({ contents: { 
                                aptitudes,
                                general, 
                                intro,
                                conocimientos,
                                educacion,
                                experiencia,
                                certificaciones
                            } 
                }) => {

    return (
        <div className="hoja-vida">
                <aside className='hoja-vida__aside'
                    dangerouslySetInnerHTML={{ __html: aptitudes }}
                ></aside>
                <main className='hoja-vida__main'>
                    <section
                        dangerouslySetInnerHTML={{ __html: general }}
                    ></section>

                    <section
                        dangerouslySetInnerHTML={{ __html: intro }}
                    ></section>

                </main>

                <section className='hoja-vida__content'
                        dangerouslySetInnerHTML={{
                            __html: conocimientos +
                                    educacion +
                                    experiencia +
                                    certificaciones }}
                ></section>
            
        </div>
    )
}

export default HojaVida