import React from "react"
import { graphql } from 'gatsby'

import Layout from "/src/layouts/main/layout/layout.js"
import Seo from "../components/seo"

import HojaVida from "../components/HojaVida/HojaVida"

const HojaVidaPage = ({ data }) => {

    const contents = data.allMarkdownRemark.nodes.reduce((prev, next) => {
        return {...prev, [next.frontmatter.section]:next.html}
    }, {});

  return (
    <Layout>
      <Seo title="Hoja de Vida JLPA" />
      {<HojaVida contents={ contents } />}
    </Layout>
  )
}

export default HojaVidaPage

export const pageQuery = graphql`query  HojaVidaQuery {
    allMarkdownRemark(
      sort: {fields: [frontmatter___date], order: DESC}
      limit: 1000
      filter: {fields: {collection: {eq: "hoja-vida"}}}
    ) {
      nodes {
        id
        frontmatter {
          section
        }
        html
      }
    }
  }
`